import {Component} from '@angular/core';
import {IonicPage, NavController, NavParams} from 'ionic-angular';
import {TokenServiceProvider} from "../../providers/token-service/token-service";
import {LocalStorageProvider} from "../../providers/local-storage/local-storage";
import {MaskTokenProvider} from "../../providers/mask-token/mask-token";
import {CryptoParamUtilProvider} from "../../providers/crypto-param-util/crypto-param-util";
import {DatePicker} from "@ionic-native/date-picker";
import {GoogleAnalyticsProvider} from "../../providers/google-analytics/google-analytics";
import {UtilitybillpayProvider} from "../../providers/utilitybillpay/utilitybillpay";


@IonicPage()
@Component({
  selector: 'page-utility-bill-payment-history01',
  templateUrl: 'utility-bill-payment-history01.html',
})
export class UtilityBillPaymentHistory01Page {

  billPaymentHistoryError01;
  fdt;
  tdt;
  fromDate;
  toDate;
  minDate;
  maxDate;

  constructor(public navCtrl: NavController,
              public navParams: NavParams,
              public tokenService: TokenServiceProvider,
              public storage : LocalStorageProvider,
              public maskTokenUtil : MaskTokenProvider,
              public cryptoUtil : CryptoParamUtilProvider,
              public utilityService : UtilitybillpayProvider,
              public datePicker: DatePicker,
              public gaProvider : GoogleAnalyticsProvider) {


    this.toDate=new Date();
    this.fromDate=new Date();

    this.fromDate.setTime(this.toDate.getTime() - 30 * 24 * 60 * 60 * 1000);
    this.fromDate.toISOString();

    this.maxDate=new Date();
    this.minDate=new Date();
    this.minDate.setFullYear(this.maxDate.getFullYear()-10);
    this.minDate.toISOString();
  }

  viewBillPaymentHistory01Action = function (fromDate, toDate) {

    let fDay = (fromDate.getDate() < 10 ? '0' : '') + fromDate.getDate();
    let tDay = (toDate.getDate() < 10 ? '0' : '') + toDate.getDate();

    let fMonth=fromDate.getMonth()+1;
    fMonth= (fMonth <10 ? '' : '')+fMonth;

    let tMonth=toDate.getMonth()+1;
    tMonth= (tMonth <10 ? '' : '')+tMonth;

    this.fdt = fDay.toString() + "/" + fMonth.toString() + "/" + fromDate.getFullYear().toString();
    this.tdt = tDay.toString() + "/" + tMonth.toString() + "/" + toDate.getFullYear().toString();

    let dateExp = new RegExp("(0?[1-9]|[12][0-9]|3[01])/(0?[1-9]|1[012])/((19|20)\\d\\d)");
    let flag = true;
    if (!dateExp.test(this.fdt))
    {
      this.billPaymentHistoryError01 = "Invalid From Date. Please Enter Valid Date."
      flag = false;
      return;
    }
    if (!dateExp.test(this.tdt ))
    {
      this.billPaymentHistoryError01 = "Invalid To Date. Please Enter Valid Date"
      flag = false;
      return;
    }

    if (flag) {
      let appData = this.storage.retrieveFromStorage("appData");
      let unMaskRefToken = this.maskTokenUtil.getUnMaskVal(appData.refreshToken);
      this.tokenService.getNewToken(unMaskRefToken)
        .then(data => {
          this.onNewTokenGenComplete(data);
        });
    }
  };

  onNewTokenGenComplete = function (data) {

    if (data.status == 0) {
      this.navCtrl.setRoot('ExpirePage');
    }

    else {
      let appData = this.storage.retrieveFromStorage("appData");
      let encFromDate=this.cryptoUtil.getEncryptVal(this.fdt);
      let encToDate=this.cryptoUtil.getEncryptVal(this.tdt);
      let unMaskToken=this.maskTokenUtil.getUnMaskVal(appData.token);
      this.utilityService.utilityBillPaymentHistory(unMaskToken, appData.userId, appData.appId, encFromDate, encToDate)
        .then(data => {
          this.onBillPaymentHistoryComplete(data);
        });
    }
  };

  onBillPaymentHistoryComplete = function (data) {
    if (data.statusCode == 1) {
      // console.log(data);
      this.navCtrl.push('UtilityBillPaymentHistory02Page', {'billPayHistoryDetails': JSON.stringify(data.object)});
    }

    else if (data.statusCode == 0) {
      this.billPaymentHistoryError01 = data.note;
    }

    else {
      this.billPaymentHistoryError01 = "System Error Contact with Administrator.";
    }
  };

  ionViewDidLoad() {
    this.gaProvider.startTrackerWithId("Utility Bill Payemnt History");
  }
}



